.card {
    flex: 1 0 20%;
    display: flex;
    text-align: center;
    flex-direction: column;
    gap: 5px;
    border-radius: 4px;
    transition: 0.3s;
    border: 2px solid $color-1;
    padding: 10px;
    max-width: 280px;
  
    h2 {
      font-size: 1rem;
    }
  
    img {
      width: 90%;
      object-fit: cover;
      border-radius: 10px;
      margin: 0 auto;
    }

    p {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 8;
      overflow: hidden;
    }
  }
  